@import '../../../base';
.cashback-summary {
  max-width: 370px;
  height: 48px;
  border-radius: 24px;
  border: solid 1px $color-line;
  padding: 6px;
  display: flex;
  img {
    height: 36px;
    width: 36px;
  }

  p {
    padding-left: 9px;
    font-size: $font-size-big;
    font-weight: $font-weight-heavy;
    color: $color-title;
  }
}