@import '../../base';

.profile-container {
  max-width: 1036px;
  margin: 120px auto;

  @media screen and (min-width: 576px) {
    padding: 0 20px;
  }

  .profile-pic-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (min-width: 768px) {
      width: 132px;
      margin-right: 63px;
    }

    .profile-pic {
      img {
        border-radius: 50%;
        width: 200px;
        height: 200px;
        object-fit: cover;
        border: 1px solid $color-title;

        @media screen and (min-width: 768px) {
          width: 132px;
          height: 132px;
        }
      }
    }

    .profile-name {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      margin: 18px 0;
      max-width: 112px;
      font-size: $font-size-regular;
      font-weight: $font-weight-semibold;
      color: $color-title;
      text-align: center;
    }

    .button-medium-color,
    .button-medium-outline {
      padding: 0;
      margin-bottom: 13px;
      p {
        font-size: $font-size-smallest;
      }
    }
  }

  .profile-detail-container {
    flex: 1;
    width: 100%;
    
    .profile-header {
      font-size: $font-size-extra-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    .cashback-top-container {
      margin-bottom: 45px;

      .cashback-how {
        display: flex;
        align-items: center;
        margin-top: 20px;
        height: 100%;

        .valid-text {
          font-size: $font-size-smallest;
          font-weight: $font-weight-normal;
          color: $color-caption;
        }

        .cashback-how-inner {
          display: flex;
          align-items: center;
          margin-top: 10px;

          &:hover {
            animation: swing 1s;
            cursor: pointer;
          }

          p {
            font-size: $font-size-smallest;
            font-weight: $font-weight-semibold;
            color: $color-hover;
          }

          img {
            width: 36px;
            height: 36px;
            margin: 0 6px;
          }

          .question {
            margin-top: -4px;
            font-size: 60px !important;
            font-weight: $font-weight-heavy !important;
            color: $color-form;
            position: absolute;
            z-index: -1;
          }
        }
        
        @media screen and (min-width: 576px) {
          justify-content: flex-end;
          margin-top: -5px;
          .cashback-how-inner {
            p {
              font-size: 12px;
            }
          }
        }

        @media screen and (min-width: 600px) {
          .cashback-how-inner {
            p {
              font-size: $font-size-smallest;
            }
          }
        }
      }
    }

    .profile-form {
      padding: 28px 0;

      .input-wrapper-desc {
        font-size: $font-size-regular;
        font-weight: $font-weight-normal;
      }

      .message {
        color: $color-error-alt;
        font-size: $font-size-regular;
        font-weight: $font-weight-semibold;
      }
    }

    .submit-container {
      margin-top: 59px;
      margin-bottom: 20px;
      display: flex;
      justify-content: center;
      @media screen and (min-width: 576px) {
        justify-content: flex-start;
      }
    }
  }

  .cashback-history-container {
    margin: 36px 0;
    display: flex;

    img {
      width: 36px;
      height: 36px;
      margin: 0 8px;
    }

    p {
      font-size: $font-size-big;
      font-weight: $font-weight-bold;
      color: $color-title;
    }
  }

  .profile-cashback-container {
    border: solid 1px $color-line;
    margin-bottom: 120px;
    
    table {
      margin: 0;

      .table-item-resp-container,
      .table-item-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
      }

      .resp-height {
        height: 60px !important;
      }

      .table-item-container ,
      tr {
        height: 84px;
      }

      td {
        padding: 0;
      }

      thead {
        th {
          border-top: none;
          border-bottom: 1px solid $color-line;
  
          text-align: center;
          font-size: $font-size-regular;
          font-weight: $font-weight-semibold;
          color: $color-title;
          padding: 0;
        }
      }
      

      tbody {
        tr {
          td {
            border-top: none;
            border-bottom: none;
            height: 84px;

            .table-item-text {
              font-size: $font-size-smallest;
              font-weight: $font-weight-normal;
              color: $color-body;
              margin: 0;
            }

            .resp-text {
              color: $color-form;
            }

            .table-amount-text {
              font-size: $font-size-regular;
              font-weight: $font-weight-semibold;
              color: $color-body;
            }

            .table-amount-minus {
              color: $color-error;
            }

            .table-completed-text {
              font-size: $font-size-regular;
              font-weight: $font-weight-normal;
              color: $color-body;
            }

            .table-pending-text {
              font-size: $font-size-regular;
              font-weight: $font-weight-normal;
              color: $color-hover;
            }
          }
        }

        .cell-colored {
          background-color: white;

          @media screen and (min-width: 576px) {
            background-color: $color-form;
          }
        }

        .cell-header {
          background-color: $color-caption;
        }
      }
    }

    .fresh-cashback-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 0 20px;
      padding-top: 56px;
      padding-bottom: 72px;

      .fresh-text {
        margin-bottom: 27px;
        font-size: $font-size-smallest;
        font-weight: $font-weight-normal;
        color: $color-caption;
        text-align: center;
      }
    }
  }
}